.formWrapper {
    background-color: #171717;
    color: rgb(215, 214, 214);
    padding: 35px 0 15px 0;
}

.formContent {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
}

.formItemWrapper {
    width: 60%;
}

.title {
    padding-top: 5px;
    box-shadow: 0 4px 10px 2px rgb(114, 114, 114);
    width: 65%;
    border-radius: 7px;
    margin-bottom: 35px;
}

.inputForm {
    width: 70%;
    margin-bottom: 10px;
    border-radius: 7px;
}

.textAreaFrom {
    resize: none;
    width: 90%;
    min-height: 100px;
    border-radius: 7px;
    margin-bottom: 10px;
}

.sendButton {
    font-size: 16px;
    color: white;
    background-color: #4b4b4b;
    cursor: pointer;
    padding: 5px;
    border: 3px solid #c5c5c5;
    border-radius: 8px;
}
.sendButton:hover{
    color: black;
    background-color: darkgrey;
}

