.mainBlock {
    height: 100vh;
    background: rgb(50, 50, 50);
    background: linear-gradient(300deg, rgba(17, 17, 17, .97) 63%, rgba(17, 17, 17, .97) 71%, rgba(255, 180, 0, 1) 72%);
}

h1 {
    line-height: 46px;
    font-size: 50px;
    text-transform: uppercase;
}

.greeting {
    width: auto;
    color: #ffb400;
}


.greeting p {
    font-size: 36px;
    line-height: 39px;
}

.inContainer {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

}

.photoContainer {
    max-width: 300px;
    text-align: center;
}

.photo {
    width: 95%;
    border-radius: 20px;
    box-shadow: 0 10px 10px 10px rgba(0, 0, 0, .5);
}


@media (max-width: 920px) {
    .mainBlock {
        background: rgba(17, 17, 17, 0.97);
    }

    .inContainer {
        flex-direction: column;
        justify-content: center;
    }

    .photoContainer {
        margin-bottom: 40px;
    }

    .photo {
        box-shadow: none;
        border: rgba(255, 255, 255, 0.2) 3px solid;
        width: 70%;
    }

    h1 {
        font-size: 25px;
        text-align: center;
        line-height: 39px;

    }

    .greeting p {
        font-size: 23px;
        text-align: center;
    }
}
