.previewWork {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    margin-bottom: 10px;
}

.previewWorkImage {
    max-width: 400px;
}

.previewWorkImage a {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-bottom: 8px;
}

.work_img {
    width: 100%;
    height: 200px;
}

.previewWorkImage:hover {
    transition: 1s;
    transform: scale(1.1);
    box-shadow: 0 15px 15px 5px rgba(187, 187, 187, 0.5);
}

.previewWorkButton {
    width: 100px;
    font-size: 20px;
    position: absolute;
    color: white;
    border: white 1px solid;
    border-radius: 5px;
    background-color: rgba(166, 166, 166, 0.5);
}

.spanItem {
    margin-top: 10px;
    font-size: 20px;
    font-weight: 600;
}

.pItem {
    font-size: 16px;
}